<template lang="pug">
	.left-sidebar
		.logo
			img(src="@/assets/logo.svg", alt="")
		.active-campaign-name
			h4 My Current Campaigns


		<!-- img(src="@/assets/Hour.svg", alt="") -->

		.all-lists
			.custom-list.clients(v-bind:class="{ opened: clientsList, loaded: clientsLoad, 'have-selected' : activeClient }")
				.visible(v-on:click="dropListClients()")
					span List of Clients
					p(v-if="activeClient") {{ activeClient.name }}

				.drop-list(v-if="clientsList")
					.list-and-search(v-if="clientsLoad")
						.search
							input(type="text", placeholder="Search...", v-model="clientSearch").search-inp

						.scroll-area
							.scroll-list
								scrolly.scroll.foo
									scrolly-viewport
										.single-item(v-for="client in filterBy(allClients, clientSearch, 'name')", v-on:click="selectClient(client.id, client.name)") {{ client.name }}
									.scroll-bar
									scrolly-bar(axis="y")

					.loader(v-if="!clientsLoad")
						.spiner
						p Loading...

			.add-new.clients(v-if="clientsLoad", v-bind:class="{active: addClient}")
				p(v-on:click="addNewClient()") Add New Client
				.new-client
					input.client-inp
					button.btn.simple(type="button") Save

			.custom-list.campaigns(v-bind:class="{ opened: campaignsList, loaded: campaignsLoad, 'have-selected' : activeCampaign }", v-if="clientsLoad && activeClient != null")
				.visible(v-on:click="dropListCampaigns()")
					span List of Campaigns
					p(v-if="activeCampaign") {{ activeCampaign.name }}

				.drop-list(v-if="campaignsList")
					.list-and-search(v-if="campaignsLoad")
						.search
							input(type="text", placeholder="Search...", v-model="campaignSearch").search-inp

						.scroll-area
							.scroll-list
								scrolly.scroll.foo
									scrolly-viewport
										.single-item(v-for="campaign in  filterBy(fakeCampaigns, campaignSearch, 'name')", v-on:click="selectCampaign(campaign.id, campaign.name)") {{ campaign.name }}
									.scroll-bar
									scrolly-bar(axis="y")

					.loader(v-if="!campaignsLoad")
						.spiner
						p Loading...

			//- router-link(to="/add-new-campaign").add-new.campaign(v-if="campaignsLoad")
			.add-new.campaign(v-if="campaignsLoad", v-on:click="addNewCampaign()", v-bind:class="{active: addCampaign}")
				p Add New Campaign

		.count-cmaping
			p Active
				br
				| Campaigns

			.count
				span /
				p 917
</template>

<script>
	import { Scrolly, ScrollyViewport, ScrollyBar } from 'vue-scrolly'
	import Vue2Filters from 'vue2-filters'
	export default {
		name: 'LeftSidebar',
		components:{
			Scrolly,
			ScrollyViewport,
			ScrollyBar
		},
		props: ['addCampaign'],
		mixins: [Vue2Filters.mixin],
		data(){
			return {
				clientsList: true,
				clientsLoad: false,
				clientSearch: '',
				activeClient: null,

				campaignsList: false,
				campaignsLoad: false,
				campaignSearch: '',
				activeCampaign: null,

				addClient: false,

				allClients: [],
				allCampaings: [],
				fakeCampaigns:[
					{
						id: 0,
						name: '360° Finance Q2 BANT'
					},
					{
						id: 1,
						name: '2020 Financial Vision'
					},
					{
						id: 2,
						name: 'Nicolas Dunlop Finance'
					},
					{
						id: 3,
						name: 'Finance Digitalised'
					},
					{
						id: 4,
						name: 'Campaign 5'
					},
					{
						id: 5,
						name: 'Campaign 6'
					},
					{
						id: 6,
						name: 'Campaign 7'
					},
					{
						id: 7,
						name: 'Campaign 8'
					},
					{
						id: 8,
						name: 'Campaign 9'
					},
					{
						id: 9,
						name: 'Campaign 10'
					}
				]
			}
		},
		created: function(){
			setTimeout(() => 
				this.getClientsList(),
			3000);
		},
		methods:{
			dropListClients(){
				this.clientsList = !this.clientsList;
				this.addClient = false;
				if (this.clientsList) {
					this.campaignsList = false;
				}
				this.addCampaign = false;
				this.$emit('addNewCampaign', {
					addCampaign: this.addCampaign
				});
				// if (this.$route.path != '/') {
				// 	this.$router.push('/');
				// }
			},
			dropListCampaigns(){
				this.campaignsList = !this.campaignsList;
				this.addClient = false;
				if (this.campaignsList) {
					this.clientsList = false;
				}
			},
			getClientsList(){
				window.api.get('client')
					.then(clients => {
						this.allClients = clients.data
						this.clientsLoad = true
					})
					.catch(clientsError => {
						console.log(clientsError);
					})
				window.api.get('client/1')
					.then(clients => {
						console.log(clients)
					})
					.catch(clientsError => {
						console.log(clientsError);
					})
			},
			selectClient(id, name){
				this.clientsList = false;
				this.activeClient = {
					id: id,
					name: name
				}
				this.campaignsList = true;


				let th = this;
				setTimeout(function(){
					th.campaignsLoad = true;
				}, 3000);
			},
			addNewClient(){
				this.addClient = !this.addClient;
				this.campaignsList = false;
				this.clientsList = false;
			},
			selectCampaign(id, name){
				this.campaignsList = false;
				this.activeCampaign = {
					id: id,
					name: name
				}
				this.$emit('getCampaigns', {
					activeCampaign: this.activeCampaign
				})
			},
			addNewCampaign(){
				this.addCampaign = !this.addCampaign;
				this.$emit('addNewCampaign', {
					addCampaign: this.addCampaign
				});
			}
		}
	}
</script>