		data(){
			return {
				hourMatrix: [],
				fullSpeed: 10000,
				logoAnim: false,
				firstAnim: false,
				secondAnim: false,
				topAnim: false,
				bottomAnim: false
			}
		},
		created: function(){
			// this.runHour();
			setTimeout(() => this.startFirstAnim(), 1000)
		},
		methods:{
			resetAnim(){
				this.firstAnim = false
				this.secondAnim = false
				this.logoAnim = false
				this.topAnim = false
				this.bottomAnim = false
				setTimeout(() => this.startFirstAnim(), 3000)
			},
			startFirstAnim(){
				this.logoAnim = true;
				this.firstAnim = true;
				setTimeout(() => this.secondAnim = true, 1000)
				setTimeout(() => { this.topAnim = true; this.bottomAnim = true} , 2000)
			}
			// runHour(){
