<template lang="pug">
	.main-layer
		LeftSidebar(
			@getCampaigns="onCampaignSelected"
			@addNewCampaign="onAddCampaign"
			:addCampaign="addCampaign"
			v-if="showLeft"
		)

		AddCampaign(
			v-bind:class="{active: addCampaign}"
			:activeCampaign="false"
			@showMain="goBack"
			v-if="showLeft"
		)

		IntegratePopup(
			v-if="showIntegrate"
		)

		LeadGoal(
			v-if="editGoal"
		)

		router-view

		RightSidebar(
			:activeCampaign="activeCampaign"
			v-if="showRight"
		)

</template>

<style lang="scss">
	@import "./mainStyles/main.scss";
</style>

<script>
	import LeftSidebar from '@/components/Left-Sidebar'
	import RightSidebar from '@/components/Right-Sidebar'
	import AddCampaign from '@/components/Add-Campaign'
	import IntegratePopup from '@/components/Integrate-Popup'
	import LeadGoal from '@/components/Lead-Goal'
	export default {
		name: 'App',
		components: {
			LeftSidebar,
			RightSidebar,
			AddCampaign,
			IntegratePopup,
			LeadGoal
		},
		data(){
			return{
				activeCampaign: null,
				showLeft: false,
				showRight: false,
				addCampaign: false,
				showIntegrate: false,
				editGoal: false
			}
		},
		created: function(){
			this.checkRouter();
		},
		watch: {
			$route() {
				this.checkRouter();
			}
		},
		methods:{
			checkRouter(){
				if (this.$route.path == '/') {
					this.showRight = true;
					this.showLeft = true;
				}else if (this.$route.path == '/add-new-campaign') {
					this.showLeft = true;
					this.showRight = false;
				}else{
					this.showRight = false;
					this.showLeft = false;
				}
			},
			onCampaignSelected (data) {
				this.activeCampaign = data.activeCampaign
			},
			onAddCampaign (data) {
				this.addCampaign = data.addCampaign
			},
			goBack(){
				this.addCampaign = false;
			}
		}
	}
</script>
